@import '../../lib/sass/px2rem';
.the_popups_container { position: fixed; top: 0; right: 0; bottom: 0; left: 0; z-index: 2; background: url(data:image/png;base64,) repeat; text-align: center; font-size: 0;
    &:after { content: ''; display: inline-block; width: 0; height: 100%; vertical-align: middle;}
    .the_popups_wrapper { width: q(670); display: inline-block; border-radius: q(18); background-color: #fff; padding: 0 q(30) q(40); vertical-align: middle;}
    .the_popups_title { position: relative;
        h1 { font-size: q(40); line-height: q(100); color: #3aa5ff; text-align: center; border-bottom: 1px solid #3aa5ff; }
        .popups_close_img { width: 100%; height: 100%;}
        .the_popups_close { width: q(34); height: q(34); position: absolute; top: q(33); right: q(20);
            &:before { content: ''; position: absolute; top: -10px; left: -10px; right: -10px; bottom: -10px;
    }}
    }
    .the_popups_main { text-align: left;}
}
