@import '../../lib/sass/px2rem';
@import '../../lib/sass/mixin';
@import '../../lib/sass/variable';
@import '../../component/the-popups/the-popups';
@import '../../component/baseRadioItem/baseRadioItem';
@import '../../component/select/select';
@import '../../component/baseBtn/baseBtn';
@import '../../component/baseInputItem/baseInputItem';
@import '../../component/addressChoice/addressChoice';

.hot-address { display: none;}
.main { background-color: $color_background_d; }
.hrd_main_tit { font-size: q(32); line-height: q(90); color: #333; margin-left: q(38);}
.hrd_list_wrapper { padding: 0 q(18) q(37); border-bottom: 1px solid #e6e6e6;}
.hrd_list_item { border: 1px solid #e6e6e6; padding: 0 q(8); background-color: $color_background;
    + .hrd_list_item { margin-top: q(18);}
    .radio_item { padding: 0 q(12);
        @import '../../component/the-promise/the-promise';
        &.first_house { padding-top: q(30); padding-bottom: q(30);}
        .promise .pro_detail { font-size: q(24);}
        + .radio_item { border-top: 1px solid #f2f2f2;}
    }
}
.hrd_bottom_btns { padding: q(60) q(20) q(68); background-color: #fff; display: flex; justify-content: space-between;
    .btn_main,
    .btn_sub { width: q(345);}
}
.the_popups_main { max-height: q(500); overflow-y: auto;
    p { font-size: q(26); line-height: 2; color: #666; margin: q(13) 0;}
}
.the_popups_btns { margin-top: q(40); text-align: center;
    .btn_main { display: inline-block; width: q(345);}
}


